<template>
    <div>
        <div class="d-block header-section">
            <h1>
                <b>Change SKU/Product Name</b>
            </h1>
            <b-col class="mb-2 col-md-6 float-right search-section">
                <b-form-group
                label
                label-cols-sm="3"
                label-align-sm="right"
                label-size="sm"
                label-for="filterInput"
                class="mb-0"
                >
                <b-input-group>
                    <b-form-input
                    v-model="filter"
                    class="form-control"
                    type="search"
                    id="filterInput"
                    placeholder="Type to Search"
                    ></b-form-input>
                    <b-input-group-append>
                    <b-button :disabled="!filter" @click="filter = ''">
                        <img src="@/assets/images/icons/cross.png" alt />
                    </b-button>
                    </b-input-group-append>
                </b-input-group>
                </b-form-group>
            </b-col>
        </div>
        <div class="container-fluid order-list">
            <!-- START: Card Data-->
            <div class="row">
                <div class="col-12 col-md-12 mt-3 mb-5">
                <div class="card pb-3">
                    <div class="p-3 list-filter">
                        <div class="btn-group">
                            <button
                            type="button"
                            class="btn btn-outline-dark rounded-btn mr-2 mob-mar" :class="{'active' : this.Alltab}" @click="onStatusFilterClicked('All')"
                            >{{this.AllText}}</button>
                        </div>
                        <div class="btn-group">
                            <input type="checkbox" id="submitted" name="orderlist" hidden>
                            <label for="submitted" type="button" class="btn btn-outline-info rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('1')">{{this.SubmittedText}}</label>                
                        </div>
                        <div class="btn-group">
                            <input type="checkbox" id="approved" name="orderlist" hidden>
                            <label for="approved" type="button" class="btn btn-outline-success rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('2')">{{this.ApprovedText}}</label>                
                        </div>
                        <div class="btn-group">
                            <input type="checkbox" id="rejected" name="orderlist" hidden>
                            <label for="rejected" type="button" class="btn btn-outline-danger rounded-btn mr-2 mob-mar" @click="onStatusFilterClicked('3')">{{this.RejectedText}}</label>                
                        </div>
                    </div>
                    <div class="overflow-auto">
                    <b-table
                        id="orderList"
                        :items="this.getOrders"
                        :filter="filter"
                        filter-debounce="1000"
                        :fields="getfields"
                        :current-page="currentPage"
                        :per-page="perPage"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        sort-icon-left
                        class="table order"
                    >
                        <template v-slot:cell(poNum)="data">
                            <span>{{ data.item.poNum }}</span>
                        </template>
                        <template v-slot:cell(actionMF)="data">
                            <router-link
                                :to="{ name: 'changeSkuProductNameView', params: {requestId: data.item.id} }"
                            >View</router-link>
                        </template>
                        <template v-slot:cell(actionAdmin)="data">
                            <router-link
                                class="mr-3"
                                v-if="data.item.status == 'Submitted'"
                                :to="{ name: 'changeSkuProductNameReview', params: {requestId: data.item.id} }"
                            >Review</router-link>
                            <router-link
                                v-else
                                :to="{ name: 'changeSkuProductNameView', params: {requestId: data.item.id} }"
                            >View</router-link>
                        </template>
                    </b-table>

                    <b-pagination
                        v-model="currentPage"
                        :total-rows="this.totalRows"
                        :per-page="perPage"
                        class="float-right mr-4"
                        first-text="First"
                        prev-text="Prev"
                        next-text="Next"
                        last-text="Last"
                    ></b-pagination>
                    </div>
                </div>
                <!-- START: Card Data-->
                </div>
            </div>
        <!-- END: Card DATA-->
        </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      /*Search */
      filter: "",
      /*Filters*/
      mfIds: [],
      orderStatus: [],
      orderFrom: null,
      orderTill: null,
      locations: [],
      /*Paginations */
      perPage: 3,
      currentPage: 1,
      totalRows: 0,
      sortBy: "requestedDate",
      sortDesc: true,
      AllText: "",
      SubmittedText: "",
      ApprovedText: "",
      InTransitText: "",
      RejectedText: "",
      ClosedText: "",
      Alltab: true,
      MFFields: [
        {
          key: "number",
          label: "Requisition Number"
        },
        {
          key: "orgName",
          label: "Manufacturer"
        },
        {
          key: "requestedDate",
          label: "Date",
          sortable: true,
        },
        {
          key: "status",
          sortable: true,
        },
        {
          key: "actionMF",
          label: "Actions",
        },
      ],
      adminFields: [
        {
          key: "number",
          label: "Requisition Number"
        },
        {
          key: "orgName",
          label: "Manufacturer"
        },
        {
          key: "requestedDate",
          label: "Date",
          sortable: true,
        },
        {
          key: "status",
          sortable: true,
        },
        {
          key: "actionAdmin",
          label: "Actions",
        },
      ],
    };
  },
  created() {
  },
  components: {},
  computed: {
    getfields: function () {
      if (this.$store.state.clientAppDecodedToken.Role == 'TsspAdmin')
        return this.adminFields;
      return this.MFFields;
    }
  },
  methods: {
    onStatusFilterClicked(status){
      if(status=="All")
      {
        this.orderStatus=[];
        this.$root.$emit('bv::refresh::table', 'orderList');
        this.Alltab = true;
        var items=document.getElementsByName('orderlist');
				for(var i=0; i<items.length; i++){
					if(items[i].type=='checkbox')
						items[i].checked=false;
				}
        return;
      }
      var statusInt = parseInt(status)
      const index = this.orderStatus.indexOf(statusInt);
      if (index > -1) {
        this.orderStatus.splice(index, 1);
        this.Alltab = false;
      }
      else{
          this.orderStatus.push(statusInt);
          this.Alltab = false;
      }
    
    //force refresh on status change
    this.$root.$emit('bv::refresh::table', 'orderList');
    },	
    getOrders: function (ctx) {
      const promise = axios({
        url: "/ReelChange/GetReelChangeRequests",
        method: "post",
        baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
        data: {
        //   mfIds: this.mfIds,
          searchText: ctx.filter,
        //   locations: this.locations,
          statuses: this.orderStatus,
          requestsFrom: this.orderFrom,
          requestsTill: this.orderTill,
          sortBy: ctx.sortBy,
          sortByDesc: ctx.sortDesc,
          pageNo: ctx.currentPage,
          pageSize: ctx.perPage,
        }
      });
      return promise.then((response) => {
        const items = response.data.miniReelChangeRequeustDtos;
        this.totalRows = response.data.totalCount;
        this.AllText = "All(" + response.data.totalCount + ")";
        this.SubmittedText = "Submitted(" + response.data.submittedCount + ")";
        this.ApprovedText = "Approved(" + response.data.approvedCount + ")";
        this.RejectedText = "Rejected(" + response.data.rejectedCount + ")";
        return items || [];
      });
    }
  },
};
</script>
